import React, { useState } from 'react'
import '../App.css'
export default function Navbar() {
  const [visibility, setvisibility] = useState("invisible")
  const [visibility2, setvisibility2] = useState("invisible")
  const [visibility3, setvisibility3] = useState("invisible")
  return (
    <div className='flex justify-between w-[100%] border-2 border-white absolute z-10 px-16 py-8'>
      <span className='text-3xl text-white fontf-ImFell'>
        Light
      </span>
      <div className='flex space-x-14'>

        <nav className='bg-none content-center'>
          <ul className='flex space-x-14 text-white '>
            <li><a className='hover:text-transparent bg-clip-text bg-gradient-to-r from-[#C824EC] to-[#DF308D]' onMouseEnter={() => setvisibility("visible")} onMouseLeave={() => setvisibility("invisible")} href="/">Home <div className={`h-1 w-4 bg-white mt-2 mx-auto ${visibility}`}></div></a></li>
            <li><a className='relative hover:text-transparent bg-clip-text bg-gradient-to-r from-[#C824EC] to-[#DF308D]' onMouseEnter={() => setvisibility2("visible")} onMouseLeave={() => setvisibility2("invisible")} href="/">Company <div className={`h-1 w-4 bg-white mt-2 mx-auto ${visibility2}`}></div></a></li>
            <li><a className='relative hover:text-transparent bg-clip-text bg-gradient-to-r from-[#C824EC] to-[#DF308D]' onMouseEnter={() => setvisibility3("visible")} onMouseLeave={() => setvisibility3("invisible")} href="/">Features <div className={`h-1 w-4 bg-white mt-2 mx-auto ${visibility3}`}></div></a></li>
          </ul>
        </nav>
        <button className='mybtn'>Signup</button>
      </div>
    </div>
  )
}
